function getRandomInt(min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min)
}

export function shuffle(arr) {
  let _arr = arr.slice()
  for (let i = 0; i < _arr.length; i++) {
    let j = getRandomInt(0, i)
    let t = _arr[i]
    _arr[i] = _arr[j]
    _arr[j] = t
  }
  return _arr
}

export function debounce(func, delay) {
  let timer

  return function (...args) {
    if (timer) {
      clearTimeout(timer)
    }
    timer = setTimeout(() => {
      func.apply(this, args)
    }, delay)
  }
}

export function findIndex(ary, fn) {
  if (ary.findIndex) {
    return ary.findIndex(fn)
  }
  /* istanbul ignore next */
  let index = -1
  /* istanbul ignore next */
  ary.some(function (item, i, ary) {
    const ret = fn.call(this, item, i, ary)
    if (ret) {
      index = i
      return ret
    }
  })
  /* istanbul ignore next */
  return index
}


export function scrollOptions(refresh = true, loadMore = true, click = false) {
    
  let refreshObj = null
  if (refresh && refresh == 'object') {
    refreshObj = refresh
  }else if (refresh) {
    refreshObj = { threshold: 50 }
  }else {
    refreshObj = false
  }

  let isNeedLoadMore = null
  if (loadMore) {
    isNeedLoadMore = loadMore
  }else {
    isNeedLoadMore = false
  }

  let options = {
          click: click,
          /* lock x-direction or y-direction when scrolling horizontally and  vertically at the same time */
          directionLockThreshold: 0,
          pullDownRefresh: refreshObj,
          pullUpLoad: isNeedLoadMore
        }

  return options
}


